@import "./base/_mixins.sass";
@import "./base/_vars.sass";
.trending {
    &__contentWrapper {
        padding-bottom: 120px; }
    &_paddingBottom {
        padding-bottom: 225px; }
    &__content {
        display: inline-block;
        margin: 15px 15px 15px 0; }
    &__content:nth-child(3n+3) {
        margin: 15px 0px 15px 15px; }
    &__wrapperImg {
        width: 370px;
        height: 370px;
        transform: scale(1);
        transition: .5s;
        &:hover {
            transform: scale(1.05); } }
    &__productTitle {
        padding: 71px 0; } }
@media (max-width: 992px) {
    .trending {
        &__wrapperImg {
            max-width: 100%;
            height: auto;
            margin: 0 auto;
            & > img {
                width: 100%;
                height: auto; } }
        &__content {
            max-width: (100% / 3 - 1%);
            margin: 15px auto 15px;
            &:nth-child(3n+3) {
                margin: 15px auto 15px; } } } }
@media (max-width: 952px) {
    .trending {
        &__content {
            max-width: (100% / 3 - 1%);
            margin: 0;
            &:nth-child(3n+3) {
                margin: 0; } } } }
@media (max-width: 700px) {
    .trending {
        &__wrapperImg {
            max-width: 100%;
            height: auto; } } }
