@import "./base/_mixins.sass";
@import "./base/_vars.sass";
.exclusive {
    &__contentWrapper {
        padding-top: 120px;
        padding-bottom: 120px;
        max-width: 1200px; }
    &__mainProduct {
        position: relative;
        float: left;
        margin-right: 15px; }
    &__products {
        display: inline-block;
        width: 50%; }
    &__position {
        position: absolute;
        top: 50%;
        left: 0;
        transform: translate(30px, -50%); }
    &__h2 {
        font-size: 36px;
        color: $dark;
        font-weight: 700; }
    &__content {
        display: inline-block;
        width: 270px;
        height: 270px;
        margin: 0px -5px 30px 15px;
        transition: .5s;
        transform: scale(1);
        &:nth-child(even) {
            margin: 0 0px 30px 30px; }
        &:hover {
            transform: scale(1.05); } }
    &__wrapperImg {
        width: 95%;
        & > img {
            width: 100%;
            height: auto; } } }
@media (max-width: 992px) {
    .exclusive {
        &__contentWrapper {
            max-width: 100%; }
        &__mainProduct {
            float: none;
            margin-right: 0;
            margin-right: 0;
            height: 500px;
            width: 100%;
            & > img {
                width: 100%;
                height: 100%; } }
        &__products {
            display: inline-block;
            width: 100%;
            text-align: center; }
        &__content {
            margin: 15px 0px 15px;
            &:nth-child(even) {
                margin: 15px 0px 15px 30px; } } } }
@media (max-width: 719px) {
    .exclusive {
        &__content {
            display: block;
            margin: 15px auto;
            &:nth-child(even) {
                margin: 15px auto; } }
        &__position {
            transform: translate(10px, -50%);
            max-width: 80%; } } }
